import { Alert } from "react-bootstrap";

const PopupAlert = ({ success, error, onClose }) => {
  return (
    <>
      {success && (
        <Alert
          variant="success"
          onClose={onClose}
          dismissible
          className="position-absolute w-100 top-0"
        >
          Vielen Dank für Ihre Feedback
        </Alert>
      )}
      {error && (
        <Alert
          variant="danger"
          onClose={onClose}
          dismissible
          className="position-absolute w-100 top-0"
        >
          {error}
        </Alert>
      )}
    </>
  );
};

export default PopupAlert;
