import { useState } from "react";
import Form from "react-bootstrap/Form";
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
import { FiThumbsDown } from "react-icons/fi";
import { apiBaseUrl } from "../api/api";
import PopupAlert from "./PopupAlert";

export default function Feedback({ animal, result, showModal, setShowModal }) {
  const [feedback, setFeedback] = useState("");
  const [error, setError] = useState(null);
  const [success, setSuccess] = useState(false);

  const handleCloseModal = () => setShowModal(false);

  const handleCloseAlert = () => {
    setError(null);
    setSuccess(false);
  };

  const sendFeedback = (e) => {
    e.preventDefault();

    console.log({ animal, result, feedback });
    setShowModal(false);
    setFeedback("");

    const formData = new FormData();
    formData.set("animal", animal);
    formData.set("result", result);
    formData.set("feedback", feedback);

    fetch(`${apiBaseUrl}/feedback`, {
      method: "POST",
      body: formData,
    })
      .then((response) => {
        if (response.ok) {
          setSuccess(true);
          setError(null);
          setTimeout(() => {
            setSuccess(false);
          }, 5000);
        } else {
          throw new Error("Error sending feedback");
        }
      })
      .catch((error) => {
        setSuccess(false);
        setError(error.message);
        setTimeout(() => {
          setError(null);
        }, 5000);
      });
  };

  return (
    <>
      <PopupAlert success={success} error={error} onClose={handleCloseAlert} />
      <Modal
        show={showModal}
        onHide={handleCloseModal}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Header closeButton>
          <Modal.Title
            id="contained-modal-title-vcenter"
            className="d-flex align-items-center gap-2"
          >
            <FiThumbsDown />
            Zusätzliches Feedback geben
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group
              className="mb-3"
              controlId="exampleForm.ControlTextarea1"
            >
              <Form.Control
                as="textarea"
                rows={3}
                value={feedback}
                onChange={(e) => setFeedback(e.currentTarget.value)}
                placeholder="Was war das Problem mit der Antwort? Wie könnte sie verbessert werden?"
              />
            </Form.Group>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleCloseModal}>
            Abbrechen
          </Button>
          <Button variant="primary" onClick={sendFeedback}>
            Feedback senden
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
}
