import "./App.css";
import { useState } from "react";
import { apiBaseUrl } from "./api/api";
import { motion } from "framer-motion";
import Button from "react-bootstrap/Button";
import OverlayTrigger from "react-bootstrap/OverlayTrigger";
import Popover from "react-bootstrap/Popover";
import { FiThumbsDown } from "react-icons/fi";
import Feedback from "./component/Feedback";



function App() {
  const [animal, setAnimal] = useState("");
  const [result, setResult] = useState("");
  const [showModal, setShowModal] = useState(false);

  const handleShow = () => setShowModal(true);

  const [loading, setLoading] = useState(null);
  // let botResult =
  //   "Für deine braune Haut und blaue Augen empfehle ich Augenbrauenprodukte. Betonte und perfekt geformte Augenbrauen können das Gesamtbild eines Make-ups erheblich verbessern.";
  // "Für deine braune Haut und blaue Augen empfehle ich Augenbrauenprodukte. Betonte und perfekt geformte Augenbrauen können das Gesamtbild eines Make-ups erheblich verbessern. Ich empfehle dir daher die wasserfeste Augenbrauen-Pomade von RIVAL loves me in der Farbe 04 dark brown sowie 03 soft brown. Beide sind wasserfest und enthalten eine 2in1 Profipinsel, um präzise und einfach zu arbeiten. Falls du es lieber natürlich magst, empfehle ich dir den Alterra NATURKOSMETIK Augenbrauen Stift in der Farbe 01 - Light brown. Diese Rezeptur ist zu 100% ohne Mikroplastik laut zertifizierter Naturkosmetik, vegan und hautverträglichkeit dermatologisch bestätigt. Alle drei Produkte eignen sich perfekt für den täglichen Gebrauch und sind leicht zu handhaben.";

  const handleSubmit = (e) => {
    e.preventDefault();
    setResult("");
    setLoading(<img src="https://storage.googleapis.com/dar-analytics.com/Spinner-Blue.gif" alt="loading" className="icon" />);

    const formData = new FormData();
    formData.set("animal", animal);

    fetch(`${apiBaseUrl}/recommendation`, {
      method: "POST",
      body: formData,
    })
      .then((response) => response.json())
      .then((botResult) => {
        setResult(botResult.result); // item, da dies vom backend so zurückgeliefert wird; für den NLP Bot muss result anstatt item genommen werden
        console.log(botResult);
      });
    // setTimeout(function () {
    //   setResult(botResult);
    // }, 1000);
  };

  const container = {
    hidden: { opacity: 1 },
    show: {
      opacity: 1,
      transition: {
        staggerChildren: 0.015,
        delayChildren: 0.01,
      },
    },
  };

  const item = {
    hidden: { opacity: 0, y: 20 },
    show: { opacity: 1, y: 0 },
  };

  return (
    <div className="App">
      <h3>Freelance Fusion</h3>
      <form onSubmit={handleSubmit} className="form-animal">
        <input
          type="text"
          name="animal"
          placeholder="Enter your questions about taxes and legal requirements for freelancers."
          required
          value={animal}
          onChange={(e) => setAnimal(e.currentTarget.value)}
        />
        <input type="submit" value="Get answers" />
      </form>
      <div className="result">
        {result ? (
          <>
            <motion.h4
              className="introductionText"
              variants={container}
              initial="hidden"
              animate="show"
              aria-label={result}
              role="heading"
            >
              {result.split("").map((char, index, array) => {
                const isLastChar = index === array.length - 1;
                return (
                  <motion.span key={index} variants={item}>
                    {char}
                    {isLastChar && (
                      <OverlayTrigger
                        trigger={['hover', 'focus']}
                        placement="bottom"
                        overlay={
                          <Popover>
                            <Popover.Header as="h3">Ihre Meinung ist uns wichtig</Popover.Header>
                            <Popover.Body>
                              Bitte lassen Sie uns wissen, was wir verbessern
                              können
                            </Popover.Body>
                          </Popover>
                        }
                      >
                        <Button
                          variant="outline-secondary"
                          className="p-1 d-inline-flex"
                          onClick={handleShow}
                        >
                          <FiThumbsDown />
                        </Button>
                      </OverlayTrigger>
                    )}
                  </motion.span>
                );
              })}
            </motion.h4>
          </>
        ) : (
          loading
        )}
      </div>
      <Feedback
        animal={animal}
        result={result}
        showModal={showModal}
        setShowModal={setShowModal}
      />
    </div>
  );
}

export default App;
